.linea {
  margin: 0px 40px;
  width: 100px;
  border-top: 1px solid #999;
  position: relative;
  top: 15px;
  float: left;
}

.leyenda {
  font-weight: bold;
  float: left;
}

.patron {
  width: 400px;
  margin: auto;
}

.cuadro {
  width: 100%;
  height: 650px;
  position: relative;
  background: linear-gradient(rgb(0, 0, 0), rgb(255, 255, 255));
  float: left;

  text-align: center;
}

.titulo {
  background: linear-gradient(45deg, rgb(0, 195, 255), rgb(255, 0, 0));
  -webkit-background-clip: text;
  color: transparent;
  font-size: 36px;
}

.boton {
  width: 30%;
}

.Letras {
  color: black;
}

.entradas {
  border-radius: 10px;
}

.cuadrodos {
  width: 100%;
  height: 583px;
  position: relative;
  background: linear-gradient(rgb(0, 0, 0), rgb(255, 255, 255));
  float: left;

  text-align: center;
}
